/**
 * poker
 */

import request from '@/api/request.js'

/**
 * GET 获取房间列表
 */
export function fetchRoomList(query) {
  return request({
    url: '/rooms',
    method: 'get',
    params: query
  })
}
/**
 * post 该玩家是否有未上链的内容
 */
export function fetchIsonchannel(data) {
  return request({
    url: '/isonchannel',
    method: 'post',
    data:data
  })
}

