/**
 * POST TRX 转 筹码
 */
export function changeBalance(data) {
  return request({
    url: '/balance',
    method: 'post',
    data
  })
}
/**
 * POST 筹码 转 TRX
 */
export function changeTRXBalance(data) {
  return request({
    url: '/trxbalance',
    method: 'post',
    data
  })
}
/**
 * 获取用户的信息
 */
export function fetchUsrInfo(query) {
  return request({
    url: '/userinfo',
    method: 'get',
    params: query
  })
}
/**
 * 创建或保存用户信息
 */
export function saveUserInfo(data) {
  return request({
    url: '/userinfo',
    method: 'post',
    data: data
  })
}
/**
 * 下发鉴权语句 用于浏览器鉴权
 */
export function fetchAuthentication(query) {
  return request({
    url: '/authentication',
    method: 'get',
    params: query
  })
}
/**
 * 用户 signTrx之后将结果提交，用于下一步下发token
 */
export function fetchSignSeed(data) {
  return request({
    url: '/signSeed',
    method: 'post',
    data: data
  })
}
/**
 * 修改 游戏信息，主要用于修改是否上链其中，更新以 tron.js 为准
 * GameState = {
    INIT: 0,
    PREPARE: 1,
    RUN: 2,
    SUBMITRESULT: 3,
    //also all finish
    CHIPRETURN: 4
  }
 */
export function saveGameInfo(data) {
  return request({
    url: '/gameinfo',
    method: 'post',
    data: data
  })
}
export function dochecktoken(usrToken) {
  return request({
    url: '/checktoken',
    method: 'post',
    data: usrToken
  })
}




